export class CompletedSegment {
  constructor (obj) {
    this.distance = obj.distance
    this.duration = obj.duration
    this.endReadDateTime = this.formatDate(obj.endReadDateTime)
    this.endReadId = obj.endReadId
    this.segmentId = obj.segmentId
    this.segmentName = obj.segmentName
    this.segmentResultId = obj.segmentResultId
    this.speedKmPHour = obj.speedKmPHour
    this.speedMinPerKm = obj.speedMinPerKm
    this.startReadDateTime = obj.startReadDateTime
    this.startReadId = obj.startReadId
    this.userFamilyName = obj.userFamilyName
    this.userGivenName = obj.userGivenName
    this.userId = obj.userId
    this.endReadUri = obj.EndReadUri
    this.startReadUri = obj.StartReadUri
    
    this.fullName = this.userGivenName + ' ' + this.userFamilyName
  }
  
  formatDate (date) {
    var d = new Date(date)
    var hh = this.addLeadingZero(d.getHours())
    var min = this.addLeadingZero(d.getMinutes())
    var sec = this.addLeadingZero(d.getSeconds())
    var time = hh + ':' + min + ':' + sec
    var newDate = d.toLocaleDateString()
    return newDate + ' ' + time
  }
  
  addLeadingZero (num) {
    if (num < 10) {
      return '0' + num
    } else {
      return num
    }
  }
}



