export class ReaderStat {
  constructor (obj) {
    this.id = obj.id
    this.latestGroomingTime = this.formatDate(obj.lastShowGrooming)
    this.name = obj.name
    this.passagesPerHourToday = this.toLocalTime(obj.passagesPerHourToday)
    this.totalPassagesToday = this.countTotalPassagesToday(this.passagesPerHourToday)
    this.todaysHighestPassagesCount = this.countTodaysHighestPassagesCount(this.passagesPerHourToday)
    this.readerName = obj.readerName
    this.xLabels = this.createArrayOfXLabels()
    this.data = this.extractDailyData(this.passagesPerHourToday)
  }
  
  formatDate (date) {
    var d = new Date(date)
    var hh = this.addLeadingZero(d.getHours())
    var min = this.addLeadingZero(d.getMinutes())
    var time = hh + ':' + min
    var newDate = d.toLocaleDateString()
    return newDate + ' ' + time
  }
  
  addLeadingZero (num) {
    if (num < 10) {
      return '0' + num
    } else {
      return num
    }
  }
  
  countTotalPassagesToday (passagesPerHour) {
    var count = 0
    for (let passage of Object.values(passagesPerHour)) {
      count += passage
    }
    return count
  }
  
  countTodaysHighestPassagesCount (passagesPerHour) {
    var highestCount = 0
    for (let passagesCount of Object.values(passagesPerHour)) {
      if (passagesCount > highestCount) {
        highestCount = passagesCount
      }
    }
    return highestCount
  }
  
  toLocalTime (obj) {
    var newObj = {}
    var d = new Date()
    var timezoneOffset = d.getTimezoneOffset() / 60
    
    var keys = Object.keys(obj)
    var startIndex = timezoneOffset
    var start = 0
    
    if (startIndex < 0) {
      for (var i = 0; i < keys.length + startIndex; i++) {
        newObj[i - startIndex] = obj[keys[i]]
      }
      for (var j = keys.length + startIndex; j < keys.length; j++) {
        newObj[start] = obj[keys[j]]
        start++
      }
    }
    return newObj
  }
  
  createArrayOfXLabels () {
    var arr = []
    var label
    
    for (var i = 0; i <= 23; i++) {
      if (i < 10) {
        label = '0' + i + ':00'
        arr.push(label)
      } else {
        label = i + ':00'
        arr.push(label)
      }
    }
    return arr
  }
  
  extractDailyData (dailyData) {
    var arr = []
    for (let data in dailyData) {
      arr.push(dailyData[data])
    }
    return arr
  }
}



