<template>
  <div>
    <div slot="header">
      <b-dropdown id="ddown3" text="Filter" variant="danger" right class="mb-2 float-right">
        <b-dropdown-item 
          @click="currentSegmentId = ''; showAllSegments = true"
          :active="currentSegmentId === ''"
          >Alla segment
        </b-dropdown-item>
        <b-dropdown-item 
          v-for="segment in segments" 
          @click="currentSegmentId = segment.id; showAllSegments = false"
          :active="currentSegmentId === segment.id"
          :key="segment.id"
          >
          {{ segment.name }}
        </b-dropdown-item>
      </b-dropdown>
    </div>
    <table class="table table-hover table-outline mb-0">
      <thead>
        <tr>
          <th>Åkare</th>
          <th>Datum</th>
          <th>Segment</th>
          <th>Distans</th>
          <th>Senaste varvtid</th>
          <th>Hastighet</th>
          <th></th>
          <th><i class="fa fa-television" aria-hidden="true"></i></th>
        </tr>
      </thead>
      <tbody>
        <tr 
          v-for="data in completedSegments" 
          :key="data.segmentResultId" 
          v-if="showAllSegments || data.segmentId === currentSegmentId"
          @click="$emit('userWasSelected', data.userId)"
          style="cursor: pointer"
          >
          <td>{{ data.fullName }}</td>
          <td>{{ data.endReadDateTime }}</td>
          <td>{{ data.segmentName }}</td>
          <td>{{ data.distance + ' m' }}</td>
          <td>{{ data.duration }}</td>
          <td>{{ data.speedKmPHour + ' km/h' }}</td>
          <td>{{ data.speedMinPerKm + ' min/km' }}</td>
          <td>
            <i 
              class="fa fa-television" 
              aria-hidden="true" 
              v-if="data.endReadUri" 
              @click="$modal.show('video-modal')">
            </i>
          </td>
        </tr>
      </tbody>
    </table>
    
    <!-- <b-modal id="modallg" size="lg" title="Modal title" v-model="openModal" @ok="openModal = false">
      
      <div class="embed-responsive embed-responsive-16by9">
        <iframe class="embed-responsive-item" src="https://www.youtube.com/embed/v1_49OFYwR8" allowfullscreen></iframe>
      </div>
      <div slot="footer"></div>
    </b-modal> -->
    <modal name="video-modal">
      <div class="embed-responsive embed-responsive-16by9">
        <iframe class="embed-responsive-item" src="https://www.youtube.com/embed/v1_49OFYwR8" allowfullscreen></iframe>
      </div>
    </modal>
  </div>
</template>
<script>
import { TagRead, CompletedSegment } from '../../models/'

export default {
  name: '',
  props: ['readers'],
  data: function () {
    return {
      completedSegments: [],
      segments: [],
      currentSegmentId: '',
      showAllSegments: true
    }
  },
  methods: {
    fetchSegments () {
      this.$http.get('Segments')
        .then(response => {
          return response.json()
        })
        .then(data => {
          this.segments = data
        })
    },
    initSignalR () {
      var connection = $.hubConnection()
      var hub = connection.createHubProxy('statisticsHub')
      connection.url = 'http://ostersundskistadium.azurewebsites.net/signalr'
      var vm = this
      hub.on('tagRead', function (tagRead) {
        // for now this will only result in the sensor being colored red and scaled up
        var tagData = new TagRead(tagRead)
        vm.$emit('receivedNewData')
        console.log(tagData)
        for (var i = 0; i < vm.readers.length; i++) {
          if (vm.readers[i].id === tagRead.readerId) {
            var index = i
            $('#reader-' + tagRead.readerId).addClass('pulse')
            setTimeout(() => {
              $('#reader-' + tagRead.readerId).removeClass('pulse')
            }, 500, index)
          }
        }
      })
      
      hub.on('segmentCompleted', function (segmentCompleted) {
        var completedSegment = new CompletedSegment(segmentCompleted)
        console.log('completed segment', completedSegment)
        // only show the 10 latest values
        if (vm.completedSegments.length < 10) {
          vm.completedSegments.unshift(completedSegment)
        } else {
          vm.completedSegments.pop(vm.completedSegments.length)
          vm.completedSegments.unshift(completedSegment)
        }
      })
      
      connection.start()
        .done(() => {
          console.log('Now connected, connection ID=' + connection.id)
        })
        .fail(() => {
          console.log('Could not connect')
        })
      console.log(vm.completedSegments)
    }
  },
  created: function () {
    this.fetchSegments()
    this.initSignalR()
    console.log(this.readers)
  }
}
</script>
<style lang="scss" scoped>
</style>


