<template>
  <b-card class="bg-success" :no-block="true">
    <div class="card-body">
      <b-dropdown class="float-right" variant="transparent p-0" right>
        <template slot="button-content">
          <i class="icon-settings"></i>
        </template>
        <b-dropdown-item>Visa sensorer</b-dropdown-item>
        <b-dropdown-item>Dölj sensorer</b-dropdown-item>
      </b-dropdown>
      <h4>Området</h4>
      <div class="map-container">
        <Reader v-for="reader in readers" :reader="reader" :key="reader.id"/>
        <img class="img-fluid mx-auto d-block border border-dark" src="../../../static/img/map.png" alt="map" />
      </div>
    </div>
  </b-card>
</template>
<script>
import Reader from './Reader'
export default {
  components: {
    Reader
  },
  data: function () {
    return {
      readers: []
    }
  },
  methods: {
    fetchReaders () {
      return this.$http.get('Readers')
        .then(response => {
          return response.json()
        })
        .then(data => {
          // this.readers = data // dont do this
          return data
        })
    }
  },
  created: function () {
    this.fetchReaders()
      .then((data) => {
        for (var i = 0; i < data.length; i++) {
          var obj = {
            settings: {
              position: {
                top: 0,
                left: 0,
                unit: '%'
              },
              bg: ''
            }
          }
          if (i === 0) {        
            obj.settings.position.top = 35
            obj.settings.position.left = 39
            obj.settings.bg = 'green'
          } else {    
            obj.settings.position.top = 46
            obj.settings.position.left = 49
            obj.settings.bg = 'brown'
          }
          var reader = Object.assign({}, data[i], obj)
          this.readers.push(reader)
        }
        this.$emit('readersWasLoaded', this.readers)
      })
  }
  
  // This below does not work.
  // Lesson learned, save this for future ref.
  // created: function () {
  //   this.fetchReaders()
  //     .then(function () {
  //       for (var i = 0; i < this.readers.length; i++) {
  //         var settings
  //         if (i === 0) {
  //           settings = {
  //             position: {
  //               top: 41,
  //               left: 52,
  //               unit: '%'
  //             },
  //             bg: 'green'
  //           }
  //         } else {
  //           settings = {
  //             position: {
  //               top: 60,
  //               left: 56,
  //               unit: '%'
  //             },
  //             bg: 'lightgreen'
  //           }
  //         }
  //         this.$set(this.readers[i], 'settings', settings)
  //       }
  //       console.log(this.readers)
  //     })
  // }
}
</script>


