export class UserStats {
  constructor (obj) {
    this.averageSpeedKmPHourToday = obj.averageSpeedKmPHourToday
    this.averageSpeedKmPHourTotal = obj.averageSpeedKmPHourTotal
    this.averageSpeedMinPerKmToday = obj.averageSpeedMinPerKmToday
    this.averageSpeedMinPerKmTotal = obj.averageSpeedMinPerKmTotal
    this.distanceToday = obj.distanceToday
    this.distanceTotal = obj.distanceTotal
    this.epc = obj.epc
    this.familyName = obj.familyName
    this.givenName = obj.givenName
    this.id = obj.id
    this.uphillToday = obj.uphillToday
    this.uphillTotal = obj.uphillTotal
    
    this.fullName = this.givenName + ' ' + this.familyName 
  }
}



