<template>
  <b-card class="bg-primary" :no-block="true">
    <div class="card-body pb-0">
      <b-dropdown class="float-right" variant="transparent p-0" right>
        <template slot="button-content">
          <i class="icon-settings"></i>
        </template>
        <b-dropdown-item>Passager</b-dropdown-item>
        <b-dropdown-item>Unika Passager</b-dropdown-item>
        <b-dropdown-item>Något annat..</b-dropdown-item>
      </b-dropdown>
      <h4 class="mb-0">{{ station.name }}</h4>
      <p>
        Antalet passager idag: <strong>{{ station.totalPassagesToday }}</strong>
        <em class="float-right mr-2">Pistad senast: {{ station.latestGroomingTime }}</em>
      </p>
    </div>
    <history-line :data="station.data" class="chart-wrapper px-3" style="height: 100px" height="100" />
  </b-card>
</template>
<script>
import HistoryLine from './HistoryLine'

export default {
  name: 'graph',
  props: {
    station: {
      type: Object
    }
  },
  data: function () {
    return {
    }
  },
  components: {
    HistoryLine
  }
}
</script>


