<template>
  <div class="animated fadeIn">
    <div class="row">
      <div class="col-md-6 col-lg-6 col-xl-5">
        <SkiMap @readersWasLoaded="readers = $event"/>
      </div>
      <div class="col-md-6 col-lg-6 col-xl-7">
        <Graphs ref="graphs"/>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12">
        <b-card header="Livedata och statistik">
          <div class="row">
            <div class="col-sm-12">
              <UserStats :userId="selectedUserId"/>
            </div>
            <div class="col-12">
              <LiveData 
                :readers="readers" 
                @userWasSelected="selectedUserId = $event"
                @receivedNewData="fetchNewData()"/>
            </div>
          </div>
        </b-card>
      </div>
    </div>
  </div>
</template>

<script>
import Graphs from '../components/graph/Graphs'
import SkiMap from '../components/map/SkiMap'
import LiveData from '../components/live-data/LiveData'
import UserStats from '../components/user-stats/UserStats'
export default {
  name: 'dashboard',
  components: {
    Graphs,
    SkiMap,
    LiveData,
    UserStats
  },
  data: function () {
    return {
      readers: [],
      selectedUserId: ''
    }
  },
  methods: {
    fetchNewData () {
      console.log('fetch new data')
      this.$refs.graphs.fetchReadersData()
    }
  }
}
</script>


