<template>
  <i :id="'reader-' + reader.id" class="fa fa-rss fa-3x sensor" :style="{'top': topPos, 'left': leftPos}" aria-hidden="true"></i>
</template>
<script>
export default {
  props: ['reader'],
  data: function () {
    return {
      topPos: '',
      leftPos: ''
    }
  },
  mounted: function () {
    this.topPos = this.reader.settings.position.top + '%'
    this.leftPos = this.reader.settings.position.left + '%'
  }
}
</script>
<style lang="scss" scoped>
</style>


