<template>
  <div>
    <b-card header="Klimat" class="border-success">
      <sync-loader :loading="isBusy" color="#20a8d8"></sync-loader>
      <div class="row animated fadeIn" v-if="!isBusy">
        <div class="col-md-4">
          <Callout variant="info">
            <div class="float-left mt-1 mr-3">
              <i class="fa fa-thermometer-half fa-3x align-middle" aria-hidden="true"></i>
            </div>
            <small class="text-muted">Temperatur</small><br>
            <strong class="h4">{{ weatherData.temperature + ' ' + weatherData.temperatureDisplayUnit }}</strong>
          </Callout>
        </div>
        <div class="col-md-4">
          <Callout variant="info">
            <div class="float-left mt-1 mr-3">
              <i class="fa fa-snowflake-o fa-3x align-middle" aria-hidden="true"></i>
            </div>
            <small class="text-muted">Snödjup</small><br>
            <strong class="h4">{{ weatherData.snowDepth + ' ' + weatherData.snowDepthDisplayUnit }}</strong>
          </Callout>
        </div>
        <div class="col-md-4">
          <Callout variant="info">
            <div class="float-left mt-1 mr-3">
              <i class="fa fa-tint fa-3x align-middle" aria-hidden="true"></i>
            </div>
            <small class="text-muted">Luftfuktighet</small><br>
            <strong class="h4">{{ weatherData.humidity + ' ' + weatherData.humidityDisplayUnit }}</strong>
          </Callout>
        </div>
      </div>
    </b-card>
  </div>
</template>
<script>
import { WeatherData } from '../../models/weather-data.model'
import { Callout } from '../'
import { SyncLoader } from 'vue-spinner/dist/vue-spinner.min'
export default {
  components: {
    Callout,
    SyncLoader
  },
  data: function () {
    return {
      weatherData: {},
      isBusy: false
    }
  },
  methods: {
    fetchWeatherData () {
      this.isBusy = true
      return this.$http.get('Weather')
        .then(response => {
          return response.json()
        })
        .then(data => {
          var weatherData = new WeatherData(data)
          this.weatherData = Object.assign({}, weatherData, this.weatherData)
          this.isBusy = false
          console.log(this.weatherData)
        })
    }
  },
  created: function () {
    this.fetchWeatherData()
  }
}
</script>
<style lang="scss" scoped>
</style>


