export class WeatherData {
  constructor (obj) {
    this.humidity = obj.humidity
    this.humidityDisplayUnit = obj.humidityDisplayUnit
    this.snowDepth = obj.snowDepth
    this.snowDepthDisplayUnit = obj.snowDepthDisplayUnit
    this.temperature = obj.temperature
    this.temperatureDisplayUnit = obj.temperatureDisplayUnit
  }  
}



