<template>
  <div class="row">
    <div class="col-12">
      <div class="row">
        <div class="col-lg-12 col-xl-6" v-for="station in stations">
            <Graph :station="station"></Graph>
        </div>
      </div>
    </div>
    <div class="col-12">
      <div class="row">
        <div class="col-md-12">
          <Climate />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Graph from './Graph'
import Climate from '../climate/Climate'
import { ReaderStat } from '../../models/reader-stat.model'
export default {
  name: 'graphs',
  data: function () {
    return {
      stations: []
    }
  },
  components: {
    Graph,
    Climate
  },
  methods: {
    fetchReadersData () {
      this.stations = []
      return this.$http.get('Readers/statistics')
        .then(response => {
          return response.json()
        })
        .then(data => {
          data.forEach(item => {
            var readerStat = new ReaderStat(item)
            this.stations.push(readerStat)
          })
        })
    }
  },
  created: function () {
    this.fetchReadersData()
  }
}
</script>
<style lang="scss" scoped>
</style>


