<template>
  <div id="">
    <b-card class="animated fadeIn" variant="accent-info" v-if="userId">
      <div slot="header">
        <div class="font-weight-bold">
          {{ userStats.fullName }}
        </div>
      </div>
      <div class="row">
        <div class="col-sm-3">
          <Callout variant="info">
            <small class="text-muted">Distans idag</small><br>
            <strong class="h4">{{ userStats.distanceToday }} m</strong>
          </Callout>
        </div>
        <div class="col-sm-3">
          <Callout variant="danger">
            <small class="text-muted">Distans totalt</small><br>
            <strong class="h4">{{ userStats.distanceTotal }} m</strong>
          </Callout>
        </div>
        <div class="col-sm-3">
          <Callout variant="info">
            <small class="text-muted">Medelhastighet <strong>idag</strong></small><br>
            <strong class="h4">{{ userStats.averageSpeedKmPHourToday }} km/h</strong>
          </Callout>
        </div>
        <div class="col-sm-3">
          <Callout variant="danger">
            <small class="text-muted">Medelhastighet <strong>totalt</strong></small><br>
            <strong class="h4">{{ userStats.averageSpeedKmPHourTotal }} km/h</strong>
          </Callout>
        </div>
        <div class="col-sm-3">
          <Callout variant="danger">
            <small class="text-muted">Snitt-tid per km <strong>idag</strong></small><br>
            <strong class="h4">{{ userStats.averageSpeedMinPerKmToday }} min/km</strong>
          </Callout>
        </div>
        <div class="col-sm-3">
          <Callout variant="danger">
            <small class="text-muted">Snitt-tid per km <strong>totalt</strong></small><br>
            <strong class="h4">{{ userStats.averageSpeedMinPerKmTotal }} min/km</strong>
          </Callout>
        </div>
        <div class="col-sm-3">
          <Callout variant="danger">
            <small class="text-muted">Höjdmeter <strong>idag</strong></small><br>
            <strong class="h4">{{ userStats.uphillToday }} m</strong>
          </Callout>
        </div>
        <div class="col-sm-3">
          <Callout variant="danger">
            <small class="text-muted">Höjdmeter <strong>totalt</strong></small><br>
            <strong class="h4">{{ userStats.uphillTotal }} m</strong>
          </Callout>
        </div>
      </div>
    </b-card>
  </div>
</template>
<script>
import { UserStats } from '../../models/user-stats.model'
import { Callout } from '../'

export default {
  props: ['userId'],
  components: {
    Callout
  },
  data: function () {
    return {
      userStats: {}
    }
  },
  watch: {
    userId (value) {
      console.log('new value', value)
      this.fetchUserData()
    }
  },
  methods: {
    fetchUserData () {
      return this.$http.get(`Users/${this.userId}/statistics`)
        .then(response => {
          return response.json()
        })
        .then(data => {
          console.log(data)
          var obj = new UserStats(data)
          this.userStats = Object.assign({}, obj)
          console.log(this.userStats)
          // this.asd.push(this.userData)
        })
    }
  }
}
</script>
<style lang="scss" scoped>
</style>


